<template>
  <div>
    <form class="col-md-12 row justify-content-between py-4 my-4" @submit.prevent="payOrder">
      <div class="col-12 col-xl-5 justify-content-center px-4">
        <h5><b class="text-primary">購物清單</b></h5>
        <br>
        <table class="table">
          <thead>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
              <td class="align-middle">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right">總計</td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>
      </div>
      <div class="col-12 col-xl-6 justify-content-center px-4">
        <h5><b class="text-primary">訂單資料</b></h5>
        <br>
        <table class="table">
          <tbody>
            <tr>
              <th>訂單編號</th>
              <td>{{ order.id }}</td>
            </tr>
            <tr>
              <th>姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>收件人電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>收件人地址</th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th>Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-secondary">付款完成</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right" v-if="order.is_paid === false">
          <button class="btn btn-outline-primary">
            <i class="fas fa-spinner fa-spin" v-if="loadingItem"></i> 確認付款去
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  data() {
    return {
      order: {
        user: {},
      },
      orderId: '',
    };
  },
  computed: {
    ...mapGetters(['loadingItem'])
  },
  methods: {
    getOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(url).then((response) => {
        vm.order = response.data.order;
        //console.log(response);
        vm.$store.dispatch('updateLoading', false);
      });
    },
    payOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
      vm.$store.dispatch('updateLoadingItem', true);
      this.$http.post(url).then((response) => {
        //console.log(response);
        if (response.data.success) {
          vm.getOrder();
          const message= response.data.message;
          const status= 'primary';
          vm.$store.dispatch('updateMessage', {message, status});
        }
        vm.$store.dispatch('updateLoadingItem', false);
      });
    },
  },
  created() {
    this.isLoading= true;
    this.orderId = this.$route.params.orderId;
    this.getOrder();
    //console.log(this.orderId);
  },
};
</script>


