<template>
  <div>
    <div class="row my-5">
      <div class="col-12 col-md-6 col-xl-4 my-2" v-for="item in filterData" :key="item.id">
        <div class="card border-0">
          <div style="height: 150px; background-size: cover; background-position: center; cursor: pointer"
            :style="{backgroundImage: `url(${item.imageUrl})`}" @click="getProduct(item.id)">
          </div>
          <div class="card-body">
            <h5 class="card-title row d-flex justify-content-between">
              <strong class="text-primary">{{ item.title }}</strong>
              <span class="badge badge-secondary float-right" @click.prevent="changeSearchText(item.category)">
                <small>{{ item.category }}</small>
              </span>
            </h5>
            <p class="text-muted">{{ item.description }}</p>
            <div class="row d-flex justify-content-between">
              <div class="h5 w-100" v-if="!item.price">NT${{ item.origin_price }} </div>
              <del class="h7 text-muted w-100" v-if="item.price">NT${{ item.origin_price }} </del>
              <div class="h5 strong text-secondary" v-if="item.price"> NT${{ item.price }} </div>
              <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addToCart(item.id)">
                <i class="fas fa-spinner fa-spin" v-if="loadingItem=== item.id"></i><i class="fas fa-shopping-cart fa-1.5x" v-else></i>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pagination.has_pre}">
          <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page-1)">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="page in pagination.total_pages" :key="page" :class="{'active': pagination.current_page=== page}">
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a></li>
        <li class="page-item" :class="{'disabled': !pagination.has_next}">
          <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page+1)">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  data() {
    return {
      
    };
  },
  computed: {
    ...mapGetters(['products', 'product', 'loadingItem', 'pagination', 'searchText']),
    filterData() {
      const vm = this;
      if (vm.searchText) {
        return vm.products.filter((item, index, array) => {
          return item.category=== vm.searchText; 
        });
      }else{
        return vm.products;
      }
    },
  },
  methods: {
    getProducts(page= 1) {
      this.$store.dispatch('getProducts', page);
    },
    getProduct(id) {
      this.$store.dispatch('getProduct', id);
    },
    addToCart(id, qty= 1) {
      this.$store.dispatch('addToCart', {id, qty});
    },
    changeSearchText(value){
      this.$store.dispatch('changeSearchText', value);
    }
  },
  created() {
    this.getProducts();
    //this.$store.dispatch('getCategory');
  },
};
</script>


