<template>
  <div>
    <div class="row justify-content-center">
      <table class="table mb-5">
        <thead>
          <tr>
            <th>建立時間</th>
            <th>訂單編號</th>
            <th>收件人姓名</th>
            <th>付款狀態</th>
            <td>更多</td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in orders" :key="key">
            <td>{{ new Date(item.create_at * 1000).toISOString().split('T')[0]}}</td>
            <td>{{ item.id}}</td>
            <td>{{ item.user.name }}</td>
            <td>
              <span v-if="item.is_paid === true" class="text-secondary">已付款 ( {{ new Date(item.paid_date * 1000).toISOString().split('T')[0]}} )</span>
              <span v-else class="text-muted">未付款</span>
            </td>
            <td>
              <button class="btn btn-outline-dark btn-sm" @click.prevent="getOrder(item)">
                <i class="fas fa-ellipsis-h"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal fade" id="orderModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">訂單資訊</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table">
              <tbody>
                <tr>
                  <th>建立時間</th>
                  <td>{{ new Date(order.create_at * 1000).toISOString().split('T')[0] }}</td>
                </tr>
                <tr>
                  <th>訂單編號</th>
                  <td>{{ order.id }}</td>
                </tr>
                <tr>
                  <th>姓名</th>
                  <td>{{ order.user.name }}</td>
                </tr>
                <tr>
                  <th>收件人電話</th>
                  <td>{{ order.user.tel }}</td>
                </tr>
                <tr>
                  <th>收件人地址</th>
                  <td>{{ order.user.address }}</td>
                </tr>
                <tr>
                  <th>Email</th>
                  <td>{{ order.user.email }}</td>
                </tr>
                <tr>
                  <th>總金額</th>
                  <td>NT${{ order.total }}</td>
                </tr>
                <tr>
                  <th>付款狀態</th>
                  <td>
                    <span v-if="!order.is_paid" class="text-muted">未付款</span>
                    <span v-else class="text-secondary">已付款</span>
                  </td>
                </tr>
                <tr>
                  <th>留言</th>
                  <td>{{ order.message }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pagination.has_pre}">
          <a class="page-link" href="#" aria-label="Previous" @click.prevent="getOrders(pagination.current_page-1)">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="page in pagination.total_pages" :key="page" :class="{'active': pagination.current_page=== page}">
          <a class="page-link" href="#" @click.prevent="getOrders(page)">{{page}}</a></li>
        <li class="page-item" :class="{'disabled': !pagination.has_next}">
          <a class="page-link" href="#" aria-label="Next" @click.prevent="getOrders(pagination.current_page+1)">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default{
  data(){
    return{
      orders: [],
      order: {
        create_at: 0,
        id: "",
        is_paid: false,
        message: "",
        total: 0,
        user: {
          address: "",
          email: "",
          name: "",
          tel: ""
        },
      },
      pagination: {}
    };
  },
  computed: {
    ...mapGetters(['loadingItem']),
  },
  methods: {
    getOrders(page= 1){
      const api= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
      const vm= this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then((response) => {
        //console.log(response.data);
        vm.orders= response.data.orders;
        vm.pagination= response.data.pagination;
        vm.$store.dispatch('updateLoading', false);
      });
    },
    getOrder(item) {
      this.order= item;
      $('#orderModal').modal('show');
    }
  },
  created() {
    this.getOrders();
  }
}
</script>


