<template>
  <div>
    <div class="row justify-content-between">
      <div class="col justify-content-center col-12 col-md-5 col-xl-6 mb-4">
        <img :src="product.imageUrl" class="img-fluid" alt="">
      </div>
      <div class="col justify-content-center col-12 col-md-7 col-xl-6">
        <h4>
          <strong class="text-primary">{{ product.title }}</strong>
          <router-link to="/products" class=""  @click.native="changeSearchText(product.category)">
            <span class="badge badge-secondary float-right">
            <small>{{ product.category }}</small>
            </span>
          </router-link>
        </h4>
        <br>
        <h5 class="text-right text-muted">{{ product.description }}</h5>
        <hr>
        <h5><strong>[產品介紹]</strong></h5>
        <br>
        <p class="text-muted">{{ product.content }}</p>
        <hr>
        <div class="d-flex justify-content-between align-items-baseline">
          <div class="h3" v-if="!product.price">{{ product.origin_price }} 元</div>
          <del class="h5 text-muted" v-if="product.price">原價 NT${{ product.origin_price }} 元</del>
          <div class="h3 strong text-secondary" v-if="product.price">特價 NT${{ product.price }} 元</div>
        </div>
        <select name="" class="form-control my-3" v-model="product.num">
          <option :value="num" v-for="num in 10" :key="num">購買{{num}}{{product.unit}}</option>
        </select>
        <div class="text-right">
          <button type="button" class="btn btn-outline-danger" @click="addToCart(product.id, product.num)">
            <i class="fas fa-spinner fa-spin" v-if="loadingItem=== product.id"></i>
            加到購物車
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  components: {
    
  },
  data() {
    return {
      
    };
  },
  computed: {
    ...mapGetters(['product', 'loadingItem']),
  },
  methods: {
    addToCart(id, qty= 1) {
      this.$store.dispatch('addToCart', {id, qty});
    },
    changeSearchText(value){
      this.$store.dispatch('changeSearchText', value);
    }
  },
  created() {

  },
};
</script>


